import React, { Component } from 'react';
import { library, dom } from '@fortawesome/fontawesome-svg-core';
import { faQuestion, faInfo } from '@fortawesome/free-solid-svg-icons';

import { faYoutube, faFacebookF } from '@fortawesome/free-brands-svg-icons';

library.add(faQuestion, faInfo, faYoutube, faFacebookF);
dom.watch();

class Footer extends Component {
    render() {
        return (
            <footer>
                <div className="container">
                    <div className="footer">
                        <div className="footer-link">
                            <a
                                href="https://www.facebook.com/XitaSGShop/"
                                title="Facebook"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <i className="fab fa-facebook-f" />
                            </a>
                            <a
                                href="https://www.youtube.com/channel/UCiG_60ziw8pwCsnFyN3wwgg"
                                title="Youtube"
                                target="_blank"
                                rel="noopener noreferrer"
                            >
                                <i className="fab fa-youtube" />
                            </a>
                            <a href="/question" title="Hướng dẫn">
                                <i className="fas fa-question" />
                            </a>
                            <a href="/contact" title="Liên hệ">
                                <i className="fas fa-info" />
                            </a>
                        </div>
                    </div>
                </div>
                <div className="footer-info">
                    <div className="container">
                        <div className="copyright">
                            © Bản quyền thuộc về Xitashop
                        </div>
                    </div>
                </div>
            </footer>
        );
    }
}

export default Footer;
